<template>
    <div class="appBox">
        <div class="text">
            <textarea v-model="content" name="" :readonly="!iscreate" placeholder="只有群主才能设置公告，字数不能大于100字符"></textarea>
            <div v-if="iscreate" class="button"><button @click="setContent">提交</button></div>
        </div>
        <p><i></i><span>仅群主可编辑</span><i></i></p>
    </div>
</template>

<script>
    import { groupNotice, setGroupNotice } from '@/api/red'
    import Clipboard from 'clipboard'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    export default {
        name: "Groupnotice",
        data()
        {
            return {
                content:'',
                iscreate:false
            }
        },

        mounted()
        {
            this.redgroup_id = this.$route.query.groupId
            this.getInfo()
        },
        methods:{
            // 获取信息
            getInfo()
            {
                groupNotice({redgroup_id:this.redgroup_id}).then(res => {
                    this.content = res.content
                    this.iscreate = res.iscreate
                })
            },
            // 设置信息
            setContent()
            {
                if (this.content.length > 100)
                {
                    Toast('公告内容最多为100个字符')
                    return
                }
                setGroupNotice({redgroup_id:this.redgroup_id, content:this.content}).then(res => {
                   Toast('更新公告成功')
                })
            }
        }
    }
</script>

<style scoped>
    .appBox {
        height: 100vh;
        position: relative;
    }
    .appBox .text {
        padding: 0.15rem 0.15rem 0;
    }
    .appBox .text textarea {
        width: 100%;
        height: 3rem;
        font-family: "PingFang SC";
        font-size: 0.14rem;
        color: #0a0000;
    }
    .appBox .text .button {
        margin-top: 0.2rem;
        text-align: center;
    }
    .appBox .text .button button {
        color: #fff;
        width: 3.45rem;
        height: .4rem;
        line-height: .4rem;
        font-size: 0.15rem;
        background: #E33C3D;
        border-radius: 0.03rem;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    }
    .appBox p {
        position: absolute;
        bottom: 0.3rem;
        left: 50%;
        margin-left: -50%;
        width: 100%;
        text-align: center;
    }
    .appBox p i {
        display: inline-block;
        width: 0.5rem;
        height: 0.01rem;
        background: #dedede;
        margin-bottom: 0.04rem;
    }
    .appBox p i:first-child {
        margin-right: 0.1rem;
    }
    .appBox p i:last-child {
        margin-left: 0.1rem;
    }
    .appBox p span {
        font-size: 0.13rem;
        color: #888;
        display: inline-block;
    }
</style>



